import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Goods from '@/components/Goods'
import ExchangeVoucher from '@/components/ExchangeVoucher'
import Voucher from '@/components/Voucher'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/goods',
      name: 'Goods',
      component: Goods
    },
    {
      path: '/voucher/exchange',
      name: 'ExchangeVoucher',
      component: ExchangeVoucher
    },
    {
      path: '/voucher',
      name: 'Voucher',
      component: Voucher
    }
  ]
})



