<template>
  <div class="goods">
    <h1>兑换券页面</h1>
    <div class="search">
      <el-form :model="searchParams" ref="searchParams" :inline="true">
        <el-form-item label="ID：" prop="id">
          <el-input v-model="searchParams.id"></el-input>
        </el-form-item>
        <el-form-item label="兑换券模板：" prop="name">
          <el-input v-model="searchParams.name"></el-input>
        </el-form-item>
        <el-form-item label="持有人：" prop="specification">
          <el-input v-model="searchParams.specification"></el-input>
        </el-form-item>
        <el-form-item label="兑换人：" prop="manufacturer">
          <el-input v-model="searchParams.manufacturer"></el-input>
        </el-form-item>
        <el-form-item label="状态：" prop="status">
          <el-select v-model="searchParams.status" placeholder="请选择">
            <el-option
              v-for="item in voucherStatusDict"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click="doSearch(searchParams)">确 定</el-button>
        <el-button @click="reset('searchParams')">重 置</el-button>
      </el-form>
    </div>
    <el-button @click="dialogFormVisible = true" type="primary" size="small">新增</el-button>
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column prop="id" label="ID"/>
      <el-table-column prop="voucherTemplateId" label="模板"/>
      <el-table-column prop="possessUid" label="持有人"/>
      <el-table-column prop="possessTime" label="持有时间"/>
      <el-table-column prop="exchangeUid" label="兑换人"/>
      <el-table-column prop="exchangeTime" label="兑换时间"/>
      <el-table-column prop="status" label="状态" :formatter="voucherStatusFormatter"/>
    </el-table>
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[1, 5, 10, 20]"
      :page-size="this.tableParams.size"
      :current-page="this.tableParams.page"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <el-dialog title="新增兑换券" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="模板">
          <el-input v-model="form.voucherTemplateId"></el-input>
        </el-form-item>
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addVoucher(form)">确 定</el-button>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api'
export default {
  created () {
    this.getVoucherStatusDict()
  },
  data () {
    return {
      searchParams: {
      },
      tableParams: {
        id: null,
        name: null,
        specification: null,
        manufacturer: null,
        status: null,
        page: 1,
        size: 5
      },
      total: 0,
      tableData: [],
      form: {
        voucherTemplateId: ''
      },
      voucherStatusDict: null,
      dialogFormVisible: false
    }
  },
  watch: {
    tableParams: {
      handler (newValue, oldValue) {
        this.listVoucherByPage(newValue)
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    doSearch (data) {
      this.tableParams = Object.assign(this.tableParams, data)
      console.info('data', data)
      console.info('params', this.tableParams)
    },
    reset (formName) {
      this.$refs[formName].resetFields()
    },
    handleSizeChange (data) {
      this.tableParams.page = 1
      this.tableParams.size = data
    },
    handleCurrentChange (data) {
      this.tableParams.page = data
    },
    voucherStatusFormatter (row, column, cellValue, index) {
      if (this.voucherStatusDict !== null) {
        for (let i = 0; i < this.voucherStatusDict.length; i++) {
          if (cellValue === this.voucherStatusDict[i].value) {
            return this.voucherStatusDict[i].label
          }
        }
      }
    },
    listVoucherByPage (params) {
      this.$http.get(api.voucher.listByPage, {params: params}).then(res => {
        this.tableData = res.body.rows
        this.total = res.body.total
      })
    },
    getVoucherStatusDict () {
      this.$http.get(api.voucher.statusDict).then(res => {
        this.voucherStatusDict = res.body
      })
    },
    addVoucher (data) {
      this.$http.post(api.goods, data)
    }
  }
}
</script>

<style scoped>

</style>



