<template>
  <div class="login">
    <h1>Welcome My Dear!!!</h1>
    <el-form ref="form" :model="form" :rules="rules" label-width="80px">
      <el-form-item label="卡密：" prop="cdKey">
        <el-input v-model="form.cdKey"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">兑换</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import api from '@/api'
export default {
  data () {
    return {
      form: {
        cdKey: ''
      },
      rules: {
        cdKey: [
          {required: true, message: '请输入礼品券卡密', trigger: 'blur'},
          { min: 5, message: '长度在5个字符以上', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.$http.post(api.voucher.exchange, this.form.cdKey).then(res => {
            console.info('res', res)
            if (res.status === 200) {
              this.$message.success('兑换成功')
            } else {
              this.$message.error('兑换失败')
            }
          })
        } else {
          this.$message.warning('请检查卡密格式')
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1{
    font-weight: bold;
  }
  .el-form-item {
    margin-bottom: 22px;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
  }
</style>



